<template>
  <img alt="Vue logo" src="./assets/pic.jpg" />
  <button v-on:click="showData">คลิกเพื่อดูข้อมูล</button>
  <h1>{{ fname }} {{ lname }}</h1>
  <h2>{{ age }}</h2>
  <button @click="pluss(5)">เพิ่มอายุทีละ 5</button>
  <button @click="minuss(10)">ลดอายุ-10</button><br>
  <button @click="plus">เพิ่มอายุ</button>
  <button @click="minus">ลดอายุ</button>

  <form>
    <label for="">ป้อนชื่อ</label>
    <input type="text" v-on:input="setfname" />
    <br />
  </form>
  <form>
    <label for="">ป้อนนามสกุล</label>
    <input type="text" v-on:input="setlname" />
    <br />
  </form>
  <form>
    <label for="">ป้อนชื่อเล่น</label>
    <input type="text" v-on:input="setnickname" />
    <br />
  </form>
  <form>
    <label for="">ป้อนอายุ</label>
    <input type="text" v-on:input="setage" />
    <br />
  </form>
  <form>
    <label for="">ป้อนวัน/เกิด/ปี</label>
    <input type="text" v-on:input="setYear" />
    <br />
  </form>
  <form>
    <label for="">ป้อนสาขา</label>
    <input type="text" v-on:input="setbranch" />
    <br />
  </form>
  <form>
    <label for="">ป้อนห้อง</label>
    <input type="text" v-on:input="setroom" />
    <br />
  </form>
  <form>
    <label for="">ป้อนคติประจำใจ</label>
    <input type="text" v-on:input="setmotto" />
    <br />
  </form>
  <form>
  <label for="">ป้อนสถานที่เรียนต่อ</label>
    <input type="text" v-on:input="setplace" />
    <br />
  </form>
  <h2>แสดงชื่อ : {{ fname }}</h2>
  <h2>แสดงนามสกุล : {{ lname }}</h2>
  <h2>แสดงชื่อเล่น : {{ nickname }}</h2>
  <h2>แสดงอายุ : {{ age }}</h2>
  <h2>แสดงวัน/เกิด/ปี : {{ Year }}</h2>
  <h2>แสดงสาขา : {{ branch }}</h2>
  <h2>แสดงห้อง : {{ room }}</h2>
  <h2>แสดงคติประจำใจ : {{ motto }}</h2>
  <h2>แสดงสถานที่เรียนต่อ : {{ place }}</h2>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      // agee: 20,
      fname: "",
      lname: "",
      age: "",
      nickname: "",
      Year:"",
      branch:"",
      motto:"",
      place:""
    };
  },
  methods: {
    showData() {
      alert(this.fname + this.lname);
    },
    plus() {
      this.age++;
    },
    minus() {
      this.age--;
    },

    pluss(val) {
      this.age += val;
    },
    minuss(val) {
      this.age -= val;
    },  
    setnickname(event) {
      console.log(event.target.value);
      this.nickname = event.target.value;
    },
    setfname(event) {
      console.log(event.target.value);
      this.fname = event.target.value;
    },
    setlname(event) {
      console.log(event.target.value);
      this.lname = event.target.value;
    },
    setage(event) {
      console.log(event.target.value);
      this.age = event.target.value;
    },
    setYear(event) {
      console.log(event.target.value);
      this.Year = event.target.value;
    },
    setbranch(event) {
      console.log(event.target.value);
      this.branch = event.target.value;
    },
    setmotto(event) {
      console.log(event.target.value);
      this.motto = event.target.value;
    },
    setplace(event) {
      console.log(event.target.value);
      this.place = event.target.value;
    },
    setroom(event) {
      console.log(event.target.value);
      this.room = event.target.value;
    },
  }
};
</script>

